.catalog {
  display: flex;
  flex-wrap: wrap;
  max-width: 944px;
  margin: 0 auto;
  padding: 50px 0;

  &__card {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    width: $card-width;
    height: 300px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: $card-margin;
    transition: color $transition-duration, transform $transition-duration;

    &:not(:nth-child(4n)) {
      margin-right: $card-margin;
    }

    &:hover {
      color: #34568b;
      transform: scale(1.2);
    }
  }
}
